ul,
li {
  list-style: none;
  padding: 0;
  font-family: "Poppins", sans-serif;
  font-size: 16px;
}
a {
  color: rgba(255, 255, 255, 0.8);
  text-decoration: none;
  position: relative;
}
a:hover {
  color: white;
}
.menu {
  position: relative;
}
.menu ul {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
}
.hamburger-menu {
  position: fixed;
  top: 0;
  right: 0;
  margin: 20px;
  display: none;
}
.alink:before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -4px;
  width: 0;
  height: 2px;
  background-color: #33cc33; /* color of the underline */
  transition: width 0.3s ease-in-out;
}
.alink.active::before {
  width: 50%;
}
.alink:hover:before {
  width: 50%; /* width of the underline */
}
@media (max-width: 998px) {
  .menu {
    display: none;
  }
  .hamburger-menu {
    display: block;
    z-index: 99;
  }
  /* ul {
    display: grid;
    grid-template-rows: repeat(auto-fill, minmax(20px, 2fr));
    grid-template-columns: repeat(auto-fill, minmax(20px, 2fr));
    grid-auto-flow: row;
    justify-content: center;
    align-items: center;
  } */
  .mobile-menu ul {
    flex-direction: column;
    align-items: center;
  }
  .mobile-menu ul li {
    margin: 10px;
  }
  .mobile-menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    z-index: 5;
    transition: 0.3s;
    background: #040404 url(/Portfolio-1.1/static/media/p3.7180b885.png) top right;
  }
}
@media (min-height: 1000px) {
  ul,
  li {
    font-size: 24px;
  }
}

